img{
    width:80px;
   
}
.user{

   font-size: 30px;
}



.hover-link{
    cursor: pointer;
    font-size: 1.25rem;
   color: white;
}

.hover-link:hover{
    color:rgb(255, 17, 0);
}

.active-link  {
    color: rgb(255, 17, 0);
}


.active-link:focus {
    color: rgb(255, 17, 0);
}

ul li {
    margin-right: 2rem;
}



.navbar{
    background: #000000;
    box-shadow: 0px 0px 50px 2px rgba(255, 255, 255, 0.171);
    z-index: 9999;
}
.parent-div{
    margin-right: 2vw;
    width: 30vw;
    max-height: 0vh;
    overflow: auto;
    background-color: rgb(0, 0, 0);
    transition: all 1s ease-in-out;
scroll-behavior: smooth;
border-radius: 0px 0px 10px 10px ;
}
.parent-div::-webkit-scrollbar {
    display: none;
  }

.height{
    max-height: 60vh;
    box-shadow: 0px 0px 20px 10px rgba(0, 0, 0, 0.658);
}
.movie-detail{
    display: flex;
    flex-direction: column;
  gap: 10px;
  padding-block: 1px;

}
.yoo{
   z-index: 1110;
    top: 66px;
    left: auto;
    border-radius: 100px;
}



#clickbox{

    color: rgb(0, 0, 0);
    background: #ffffff;
    border: 1px solid rgba(255, 255, 255, 0.637);
    border-radius: 3px;
   width: 120%;
   font-size: 1.1rem;
   font-weight: 400;
   transition: all .5s ease-in-out;
}
#clickbox::placeholder{
    
    color: rgba(39, 39, 39, 0.692);

   
}
.s-icon{
         
    background-color: #f02106e3;
    padding-inline: 5%;
    border-radius: 5px 0px 0px 5px !important;
}
.form-control {
    border-radius: 0px 5px 5px 0px !important;
}


@media (max-width : 990px){
    .search1{
        z-index: 99;
          position: sticky;
          top: 65px;
          width: 100%;
          text-align: center;
        border-radius: 0px;
        transition: all 0.5s ease-in-out;
        }
        .search2{
            top: 0px;
        }
        .search{
            display: none !important;
        }
        
}

   
 
 

.not-found{
    width: 100%;
    height: 30vh;
    text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

